export function isUsernameCorrect(
  pwd: string
): { isValid: true } | { isValid: false; message: string } {
  if (pwd.length < 8) {
    return {
      isValid: false,
      message: "'   ",
    };
  }
  if (pwd.length > 20) {
    return {
      isValid: false,
      message: "'   ",
    };
  }
  if (!/^[a-zA-Z0-9]+$/.test(pwd)) {
    return {
      isValid: false,
      message: "'       . ",
    };
  }
  return {
    isValid: true,
  };
}
